@font-face {
  font-family: "font";
  src: url("assets/font.woff2");
}

html{
  font-size: 20px;
  font-family: "font",Arial, Helvetica, sans-serif;
}

body{
  margin: 0;
  overflow-x: hidden;
  transition: background-color 0.5s;
}

::-webkit-scrollbar{
  display: none;
}

.start{
  opacity: 0;
  overflow: hidden;
  transition: opacity 0.5s;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: calc(100 * var(--vh));
  min-width: 50vw;
  float: right;
}

.start > h1{
  margin: 0;
  font-size: 4rem;
  line-height: 4rem;
  text-transform: uppercase;
  font-weight: 900;
  word-break: break-all;
  letter-spacing: -0.1rem;
}

.start > h1 > a[myhref]{
  color:black;
  text-decoration:none;
  display: inline-block;
  transition: transform 0.1s;
  cursor: pointer;
}

.start > h1 > a[myhref] > *{
  pointer-events: none;
}

.start > h1 > a[myhref]:hover{
  transform: skew(-30deg,0) translate(1rem,0);
}

.start > h1 > a > span[t]{
  font-size: 4.4rem;
  font-family: 'Times New Roman', Times, serif;
  font-weight: 100;
  font-style: italic;
}

.project{
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: scroll;
  position: fixed;
  top: 0;
  left: 0;
  background-color: white;
  transition: transform 0.5s;
  transform: translate(0,0);
}

.project.enter{
  transform: translate(0,100%);
}
.project.exit{
  transform: translate(0,-100%);
}

.back{
  position: fixed;
  left: calc(800px + 2rem);
  bottom: 0;
  font-size: 2rem;
  font-weight: 900;
  margin: 1rem;
  line-height: 2rem;
  cursor: alias;
}

article{
  font-family: "font";
}

article > h1{
  margin: 1rem;
  text-transform: uppercase;
  font-size: 1.5rem;
}
article > section > .image-repeater{
  height: 300px;
  background-repeat: repeat;
  background-size: auto 100%;
  animation: repeater 10s infinite linear;
}
@keyframes repeater {
  0%   {background-position: 0% 0%;}
  100% {background-position: 100% 0%;}
}
article > footer{
  margin: 1rem;
  margin-top: 2rem;
  text-transform: uppercase;
  max-width: 400px;
  height: 9rem;
  font-size: 0.8rem;
  border-radius: 0.5rem;
  overflow: hidden;
  background-color: #000;
  color: white;
  display: flex;
  align-items: flex-end;
}

article > footer > .l{
  width: 60%;height: 100%;
}

article > footer > .l > video{
  object-fit: cover;width: 100%;height: 100%;border-radius: 0.5rem 0 0 0.5rem;
}

article > footer > .r{
  margin:0.5rem;
}

article > section{
  max-width: 800px;
  margin: 1rem;
  overflow: hidden;
}

article > section > a{
  color: black;
}

article > section > iframe{
  width: 100%;height: calc(100vh - 5rem);
  overflow: scroll;
}
article > section > iframe::-webkit-scrollbar{
  display: none;
}

@media only screen and (max-width:980px){
  .back{
    right: 0;
    left: auto;
    background-color: rgba(255,255,255,0.5);
    backdrop-filter: blur(5px);
  }
}